.movieTrendList {
  
  list-style: none;
  display: grid;
  grid-template-columns: 300px ;
  column-gap: 10px;
  justify-content: space-around;
  row-gap: 15px;
  padding: 0;
}

@media screen and (min-width: 768px) {
  .movieTrendList {
    grid-template-columns: 300px 300px ;
  }

}

@media screen and (min-width: 1048px) {
  .movieTrendList {
    grid-template-columns: 300px 300px 300px 300px;
  }

}

.movieTrendItem {
  margin-bottom: 10px;
  padding: 10px;
  margin: 2px;
  border-bottom: solid 1px black;
}

.movieTrendLink {
  color: black;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.movieTrendItem:hover {
  cursor: pointer;
}
.movieTrendItem:hover .movieTrendLink {
  color: rgb(237, 24, 24);
}


.movieTrend__img{
  width: 150px;
  margin-bottom: 20px;
}

.movieTrend__btn{
  display: block;  
  margin:  50px auto 40px auto;
  background-color: #ffffff00;
  appearance: none;
	border: .2em solid rgb(237, 24, 24);
	padding: .85em 1.5em;
	color: rgb(237, 24, 24);
	border-radius: 2em;
	transition: 0.3s;
	height: 40px;

}

.movieTrend__btn:hover,
.movieTrend__btnL:focus,
.movieTrend__btn:active{
cursor: pointer;
background: rgba(219, 24, 24, 0.888);
color: #fff;
}


.upBtn__box{
  position: fixed;
  right: 20px;
  bottom: 25px;
  width: 65px;
  height: 65px;


}

.upBtn{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
    width: 40px;
    height: 40px;
  
    border-radius: 50%;
    color: rgb(230, 0, 0);
  
    animation-name: btnAnimation;
    animation-duration: .7s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
  
  } 
  
  .upBtn:hover,
  .upBtn:focus{
    cursor: pointer;
  }
  
  
  
  @keyframes btnAnimation {
  0%   {  box-shadow: rgba(255, 0, 0, 0.3) 0px 0px 0px 1px, rgba(255, 0, 0, 0.3) 0px 0px 0px 3px;}
  
    100% {  box-shadow:  rgba(255, 0, 0, 0.3) 0px 0px 0px 5px, rgba(255, 0, 0, 0.3) 0px 0px 0px 12px;} 
  }